import React from "react";

async function get_data(url) {
  const resp = await fetch(url);
  return resp.json();
}

const fic_data = await get_data('fanfic_data.json');

const FanficTable = () => {
  let fic_list = Object.entries(fic_data.fics)
  fic_list = fic_list.map((fic) => (fic[1]))
  console.log(fic_list)
  return (
    <div>
      {fic_list.map((fic) => <div><hr />{fic.title}<hr /></div>)}
    </div>
  )
}

export default FanficTable;